.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background: rgb(0, 0, 0);
    padding: 20px;
    border-radius: 5px;
    width: 80%;
    max-width: 800px;
    max-height: 80%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
  }
  
  .close-button {
    align-self: flex-end;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .details-section {
    display: flex;
    justify-content: space-between;
  }
  
  .details-column {
    width: 48%;
  }