.inventory-management {
  padding: 20px;
  background-color: rgb(1, 31, 56);
}

.form {
  margin-bottom: 20px;
}

.inventory-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Create two equal columns */
  gap: 10px; /* Add spacing between items */
  padding: 6px;
  color: rgb(0, 233, 233);
}

.inventory-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.inventory-item span {
  padding: 7px;
  flex-grow: 1;
  text-align: center;
}

.inventory-item button {
  margin-top: 5px;
}