body {
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: #021b41;
  color: #ffffff;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.logo {
  width: 150px;
  height: 150px;
  margin-right: 145px;
  justify-content: center;
  align-items: center;
}

h1, h2, h3, h4 {
  color: #ff4d4f;
}

nav {
  background-color: #2c3e50;
  padding: 10px;
  width: 110%;
  height: 100%;
  border-radius: 5px;
  margin-bottom: 20px;
  display: flex; /* Add this line to make the buttons in a row */
  gap: 10px; /* Add this line to add space between buttons */
}

nav button {
  background-color: #ff4d4f;
  color: #ffffff;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
  height: 60px;
}

nav button:hover {
  background-color: #e63946;
}

.form-two-columns, .form {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  background-color: #030a11;
  padding: 20px;
  border-radius: 5px;
}

.form-two-columns button {
  height: 35px;
}

.column {
  flex: 1;
  min-width: 300px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #ff4d4f;
}

input, select, textarea, button {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ff4d4f;
  border-radius: 5px;
  background-color: #34495e;
  color: #ffffff;
}

button {
  background-color: #ff4d4f;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #e63946;
}

.student-list.two-columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  font-size: large;
}

.student-item, .inventory-item {
  background-color: #030a11;
  padding: 15px;
  border-radius: 5px;
}

.student-item button {
  width: 415px;
  padding: 5px;
  border-radius: 15px;
}

.student-details .section {
  margin: 20px 0;
}

.checkin-window {
  max-height: 200px;
  overflow-y: auto;
  background-color: #34495e;
  padding: 10px;
  border-radius: 5px;
}

.search-bar {
  display: flex;
  align-items: center;
  text-align: center;
  width: 60%;
  gap: 10px; /* Add some space between the input and button */
}

.search-bar input[type="text"] {
  flex: 1;
  width: 100%;
  padding: 10px;
  border: 1px solid #ff4d4f;
  border-radius: 5px;
  background-color: #071c31;
  color: #ffffff;
}

.search-bar button {
  padding: 1px;
  text-align: center;
  border: 1px solid #ff4d4f;
  border-radius: 5px;
  background-color: #34495e;
  color: #ffffff;
  cursor: pointer;
  width: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.search-bar button:hover {
  background-color: #ff4d4f;
  border-color: #ff4d4f;
}

.student-checkin {
  padding: 20px;
}

.student-checkin input {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ff4d4f;
  border-radius: 5px;
  background-color: #34495e;
  color: #ffffff;
}

.student-checkin ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.student-checkin li {
  padding: 10px;
  margin-bottom: 10px;
  background-color: #2c3e50;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.student-checkin li:hover {
  background-color: #1a2b44;
}

.student-checkin li.flash {
  animation: flash 0.5s;
}

@keyframes flash {
  0% { background-color: #ff4d4f; }
  100% { background-color: #2c3e50; }
}

.student-checkin label {
  display: block;
  margin-top: 2px;
  color: #ff4d4f;
}

.student-checkin select, .student-checkin button {
  width: 50%;

  
  border: 1px solid #ff4d4f;
  border-radius: 5px;
  background-color: #34495e;
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}





.message {
  margin-top: 2px;
  color: red;
  font-size: 24px;
  text-align: center;
  border: 4px solid #ff4d4f;
  border-radius: 12px;
  width: 100%;
}

textarea {
  height: 200px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  width: 70%;
  max-width: 500px;
  max-height: 80%;
  overflow-y: auto;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: rgba(3, 3, 3, 0.705);
  padding: 20px;
  border-radius: 5px;
  width: 80%;
  max-width: 800px;
  max-height: 80%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.close-button {
  align-self: flex-end;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.details-section {
  display: flex;
  justify-content: space-between;
}

.details-column {
  width: 48%;
}

.calendar-container {
  display: flex;
  gap: 20px;
}

.notes-section {
  flex: 1;
}

.has-note {
  background-color: #ff4d4f !important;
}


ul {
  list-style: none;
  padding: 0;
}

li {
  background-color: #34495e;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 5px;
}

.react-calendar {
  background-color: #144a3f; /* Dark blue color */
  padding: 30px;
  border-radius: 5px;
  width: 50%;
}


.react-calendar__tile{
 height: 60px;
}

.react-calendar__tile--now {
  background: #15b3a6;
}

.react-calendar__month-view__days__day--weekend:not(
    .react-calendar__month-view__days__day--neighboringMonth
  ) {
  color: rgb(255, 255, 255) !important;
}

.notes-section textarea{
  background-color: #023c77;
  padding: 20px;
  border-radius: 5px;
  font-size: 1.8em;
  height: 400px;
}