.newsletter {
  padding: 20px;
  background-color: rgb(0, 0, 0);
}

.newsletter label {
  display: block;
  margin-top: 10px;
}

.newsletter input[type="text"],
.newsletter textarea {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #bd4747;
  background-color: rgb(0, 0, 0);
  font-size: 1.5em;

  
  border-radius: 5px;
}

.newsletter textarea {
  height: 100px;
}

.newsletter ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.newsletter li {
  margin-top: 10px;
}

.newsletter button {
  margin-top: 20px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.newsletter button:hover {
  background-color: #0056b3;
}
textarea{
  background-color: #08223d;
}