.reports {
  padding: 20px;
}

.sales-list {
  display: flex;
  flex-wrap: wrap;
}

.sales-list.two-columns .sales-item {
  width: 48%;
  margin: 1%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}

.sales-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sales-item span {
  flex-grow: 1;
}

.sales-item button {
  margin-left: 5px;
}