body {
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Changed from center to flex-start */
    height: 100vh;
    margin: 0;
  }
  
  .keypad {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ccc;
    background-color: black;
    width: 500px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 330px; /* Added margin-top to push the keypad down */
  }
  
  .display {
    width: 100%; /* Adjusted width to fit the container */
    height: 50px;
    border: 1px solid #ccc;
    margin-bottom: 10px;
    text-align: center;
    font-size: 24px;
    line-height: 40px;
    color: green;
  }
  
  .buttonspad {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    width: 100%/* Adjusted width to fit the container */;
    color:green;
  }
  
  .buttonspad button {
    width: 100%; /* Adjusted width to fit the container */
    height: 60px;
    font-size: 18px;
    cursor: pointer;
  }